import * as Swarms from "@hervan/swarms";
import { numberlist } from "@hervan/utils";
import type { BlueprintPipsProps, BlueprintProps } from "../../../Blueprint";
import {
  Pip,
  blueprintColor,
  blueprintPips,
  blueprintPipsCoords,
} from "../../../Blueprint";
import { emojiFromSpecies } from "../../../Species";

type Action = Swarms.Action.Action;
type SlotState = Swarms.Player.Grid.Slot.Slot<Action>;
type Species = Swarms.Player.Species.Species;
type Player = Swarms.Player.Player;
type TerrainMap = Swarms.Map.TerrainMap;

const { scienceScore } = Swarms.Player.Grid.Slot;
const { blueprintMastery } = Swarms.Player.Grid;
const { Action } = Swarms.Action;
const { addCoordinates, sameCoordinates } = Swarms.Map;

const researchOrder = ["👥", "🏙️", "⚓️", "🤖"];

const BlueprintGridPips = ({
  blueprint,
  coord,
  selected,
}: BlueprintPipsProps) => (
  <>
    {blueprintPipsCoords().map((pipGrid, i) => {
      const filledPip =
        blueprint !== undefined &&
        blueprintPips(blueprint).some((pip) =>
          sameCoordinates(pip, pipGrid.pip)
        );
      return (
        <Pip
          key={i}
          coord={addCoordinates(coord, pipGrid.coord)}
          color={filledPip ? blueprintColor(blueprint) : blueprintColor()}
          selected={selected}
        />
      );
    })}
  </>
);

const BlueprintGrid = ({ blueprint, selected }: BlueprintProps) => (
  <svg
    height="6vmin"
    width="12vmin"
    preserveAspectRatio="xMinYMin slice"
    viewBox="0 0 10 12"
  >
    <BlueprintGridPips
      coord={{ row: 3, col: 5 }}
      blueprint={blueprint}
      size={8}
      selected={selected}
    />
  </svg>
);

type SlotProps = {
  map: TerrainMap;
  player: Player;
  slot: SlotState;
  selected: boolean;
  species: Species;
};

export const Slot = ({ map, player, slot, selected, species }: SlotProps) => (
  <>
    <div>
      <BlueprintGrid blueprint={slot.blueprint} selected={selected} />
    </div>
    <div>
      <span style={{ float: "right" }}>
        {slot.action === Action.Science
          ? `${scienceScore(player, map)[slot.row]}${researchOrder[slot.row]}`
          : slot.action === Action.Swarm && slot.row === 0
          ? emojiFromSpecies(species)
          : null}
        &nbsp; &nbsp;
      </span>
      <br />
      <span style={{ float: "right" }}>
        {numberlist(8).map((i) => (
          <span key={i}>
            {(slot.blueprint === undefined
              ? slot.value
              : blueprintMastery(player.grid, slot.blueprint, slot.value)) > i
              ? "◼️"
              : "▢"}
          </span>
        ))}
        &nbsp;
      </span>
    </div>
  </>
);
