import { numberlist } from "@hervan/utils";
import { Action, ActionList, resourceFromAction } from "../../Action";
import { Blueprint } from "../../Blueprint";
import { emptyBag } from "../../Map/Terrain/Resource";
import { Slot } from "./Slot";

export const GRID_SIZE = 4;

type GridRow = {
  [k in Action]: Slot<Action>;
};

const emptyGridRow: (rowNumber: number) => GridRow = (rowNumber) =>
  ActionList.reduce(
    (row: GridRow, action, colNumber) => ({
      ...row,
      [action]: {
        action,
        row: rowNumber,
        blueprint: undefined,
        value: Math.ceil((rowNumber + colNumber) / 1.75),
      } as Slot<typeof action>,
    }),
    {} as GridRow
  );

export type TechGrid = GridRow[];

export const playerStartingGrid: () => TechGrid = () =>
  numberlist(GRID_SIZE).map((i) => emptyGridRow(i));

const isInitialPosition = (grid: TechGrid, row: number, action: Action) =>
  row === 0 &&
  action === Action.Swarm &&
  grid[row][action].blueprint === undefined;

const isAdjacentToExistingUpgrade = (
  grid: TechGrid,
  row: number,
  col: number
) =>
  [
    ...(row === 0 ? [] : [grid[row - 1][ActionList[col]]]),
    ...(row === GRID_SIZE - 1 ? [] : [grid[row + 1][ActionList[col]]]),
    ...(col === 0 ? [] : [grid[row][ActionList[col - 1]]]),
    ...(col === GRID_SIZE - 1 ? [] : [grid[row][ActionList[col + 1]]]),
  ].some((s) => s.blueprint !== undefined);

export const isUniqueBlueprint = (
  grid: TechGrid,
  row: number,
  action: Action,
  blueprint: Blueprint
) =>
  grid[row][action].blueprint === blueprint ||
  (grid.every((row) => row[action].blueprint !== blueprint) &&
    ActionList.every((a) => grid[row][a].blueprint !== blueprint));

export const isValidSpaceSelection = (
  grid: TechGrid,
  row: number,
  action: Action,
  blueprint: Blueprint
) => {
  const col = ActionList.findIndex((a) => a === action);

  const previouslyUpgradedAction = grid[row][action].blueprint === blueprint;
  if (previouslyUpgradedAction) {
    return action !== Action.Science;
  }
  const upgradeRequirements =
    grid[row][action].blueprint === undefined &&
    isUniqueBlueprint(grid, row, action, blueprint);
  const adjacencyRequirement =
    isInitialPosition(grid, row, action) ||
    isAdjacentToExistingUpgrade(grid, row, col);
  return upgradeRequirements && adjacencyRequirement;
};

export const blueprintMastery: (
  grid: TechGrid,
  blueprint: Blueprint | undefined,
  slotValue: number
) => number = (grid, blueprint, slotValue) =>
  blueprint === undefined
    ? 0
    : slotValue +
      grid
        .flatMap((row) => ActionList.map((action) => row[action]))
        .filter((action) => action.blueprint === blueprint).length;

export const actionCost = (slot: Slot<Action>, amount: number = 0) => ({
  ...emptyBag,
  [resourceFromAction(slot.action)]: slot.value + amount,
});

export function upgradeGrid(
  grid: TechGrid,
  slot: Slot<Action>,
  token: Blueprint
): TechGrid {
  if (token === undefined) {
    return grid;
  } else if (slot === undefined) {
    return grid;
  } else
    return grid.map((originalRow, i) =>
      ActionList.reduce(
        (newRow, action) => ({
          ...newRow,
          [action]:
            i === slot.row && action === slot.action
              ? {
                  ...originalRow[action],
                  blueprint: token,
                }
              : originalRow[action],
        }),
        originalRow
      )
    );
}

export * as Slot from "./Slot";
