import { Blueprint } from "../../Blueprint";
import { Coordinates, TerrainMap } from "../../Map";
import { Resource, ResourceBag, emptyBag } from "./Resource";

export enum TerrainType {
  Water = "water",
  Forest = "forest",
  Desert = "desert",
  Mountain = "mountain",
  City = "city",
}

export const TerrainList: TerrainType[] = [
  TerrainType.Water,
  TerrainType.Forest,
  TerrainType.Desert,
  TerrainType.Mountain,
];

export const TerrainSmoothing: TerrainType[] = [
  TerrainType.Water,
  TerrainType.Desert,
  TerrainType.Forest,
  TerrainType.Mountain,
];

type FreeTerrain = {
  state: "free";
};

type OccupiedTerrain = {
  state: "occupied";
  owner: number;
};

type BuiltTerrain = {
  state: "built";
  blueprint: Blueprint;
};

type TerrainState = FreeTerrain | OccupiedTerrain | BuiltTerrain;

export type Terrain = {
  type: TerrainType;
  coord: Coordinates;
} & TerrainState;

export const terrainFromMap: (
  map: TerrainMap,
  { row, col }: Coordinates
) => Terrain = (map, { row, col }) => map[row][col];

type BoundingBox = {
  topLeft: Coordinates;
  bottomRight: Coordinates;
};

export const getBoundingBox: (swarm: Coordinates[]) => BoundingBox = (
  swarm
) => ({
  topLeft: {
    row: Math.min(...swarm.map((o) => o.row)),
    col: Math.min(...swarm.map((o) => o.col)),
  },
  bottomRight: {
    row: Math.max(...swarm.map((o) => o.row)),
    col: Math.max(...swarm.map((o) => o.col)),
  },
});

export const terrainToIndex = (terrain: TerrainType) =>
  TerrainSmoothing.findIndex((t) => t === terrain);

export const terrainFromResource: (resource: Resource) => TerrainType = (
  resource
) => {
  switch (resource) {
    case Resource.Food:
      return TerrainType.Water;
    case Resource.Log:
      return TerrainType.Forest;
    case Resource.Energy:
      return TerrainType.Desert;
    case Resource.Ore:
      return TerrainType.Mountain;
    case Resource.Coin:
      return TerrainType.City;
  }
};

export const resourceFromTerrain: (type: TerrainType) => Resource = (type) => {
  switch (type) {
    case TerrainType.Water:
      return Resource.Food;
    case TerrainType.Forest:
      return Resource.Log;
    case TerrainType.Desert:
      return Resource.Energy;
    case TerrainType.Mountain:
      return Resource.Ore;
    case TerrainType.City:
      return Resource.Coin;
  }
};

export const resourcesFromTerrains = (
  map: TerrainMap,
  terrains: Coordinates[]
) =>
  terrains.map((coord) => resourceFromTerrain(terrainFromMap(map, coord).type));

export const resourcesFromMap: (
  map: TerrainMap,
  selection: Coordinates[],
  bag?: ResourceBag
) => ResourceBag = (map, selection, bag = emptyBag) =>
  resourcesFromTerrains(map, selection).reduce(
    (acc, resource) => ({ ...acc, [resource]: acc[resource] + 1 }),
    bag
  );

export * as Resource from "./Resource";
