import { Coordinates } from "../Map";
import {
  Resource,
  ResourceBag,
  ResourceList,
  emptyBag,
  payWithCoins,
} from "../Map/Terrain/Resource";
import { Slot } from "../Player/Grid/Slot";

export enum Action {
  Swarm = "swarm",
  City = "city",
  Trade = "trade",
  Science = "science",
}

export const ActionList: Action[] = Object.values(Action);

export type Selection<T extends Action> = {
  slot?: Slot<T>;
  location: Coordinates[];
  destination: Coordinates[];
  cost: { resources: ResourceBag; time: number };
};

export const noSelection: Selection<Action> = {
  slot: undefined,
  location: [],
  destination: [],
  cost: {
    resources: emptyBag,
    time: 0,
  },
};

export const resourceFromAction: (action: Action) => Resource = (action) => {
  switch (action) {
    case Action.Swarm:
      return Resource.Food;
    case Action.City:
      return Resource.Log;
    case Action.Trade:
      return Resource.Energy;
    case Action.Science:
      return Resource.Ore;
    default:
      return Resource.Coin;
  }
};
// http://localhost:3000/171752660538/0
// http://localhost:3000/506658811320/0
export const actionTime = (
  selection: Selection<Action>,
  availableResources: ResourceBag
) =>
  selection.cost.time +
  [...selection.location, ...selection.destination].length *
    (selection.slot?.value || 0) +
  ResourceList.reduce((acc, r) => acc + selection.cost.resources[r], 0) -
  payWithCoins(selection.cost.resources, availableResources);
