import * as Swarms from "@hervan/swarms";
import { numberlist } from "@hervan/utils";
import { TIME_EMOJI, fontSize } from "../theme";
import { Grid } from "./Grid";

type TerrainMap = Swarms.Map.TerrainMap;
type PlayerState = Swarms.Player.Player;
type Resource = Swarms.Map.Terrain.Resource.Resource;

const { ActionList, actionTime, resourceFromAction } = Swarms.Action;
const { resourcesFromMap } = Swarms.Map.Terrain;
const { Resource, ResourceList } = Swarms.Map.Terrain.Resource;

const COINS_LINE = 10;
const COINS_LINE_GROUP = 5;

const emojiFromResource = (type: Resource) => {
  switch (type) {
    case Resource.Food:
      return "🍗";
    case Resource.Log:
      return "🪵";
    case Resource.Energy:
      return "⚡️";
    case Resource.Ore:
      return "🪨";
    case Resource.Coin:
      return "🟡";
  }
};

interface PlayerProps {
  map: TerrainMap;
  player: PlayerState;
  selectSlot: (...args: any[]) => void;
  displayResult: boolean;
  isActive: boolean;
  discardSelection: (...args: any[]) => void;
}

export const Player = ({
  map,
  player,
  selectSlot,
  displayResult,
  isActive,
  discardSelection,
}: PlayerProps) => (
  <table
    key={`player${player.id}`}
    style={{
      fontSize: `${fontSize}vmin`,
    }}
  >
    <thead>
      <tr>
        {ActionList.map((action, i) => (
          <th key={i} style={{ textAlign: "right" }}>
            {`${action} ${emojiFromResource(resourceFromAction(action))}`}
          </th>
        ))}
      </tr>
    </thead>
    <tbody>
      <Grid map={map} player={player} selectSlot={selectSlot} />
      <tr key="coinshead">
        <td key="coins">
          <span style={{ fontWeight: "normal" }}>
            coins {player.coins - player.coinsSpent}
            {displayResult ? "🏆" : null}
          </span>
        </td>
      </tr>
      <tr key="coins">
        {numberlist(4).map((i) => (
          <td key={`coins${i}`}>
            {numberlist(COINS_LINE_GROUP).map((line) => (
              <span
                key={`${i}x${line}`}
                style={{ fontSize: `${fontSize - 0.25}vmin` }}
              >
                {numberlist(COINS_LINE).map((linecoin) => {
                  const coin =
                    line * 4 * COINS_LINE + i * COINS_LINE + linecoin;
                  // return `${line} ${i} ${linecoin}|`;
                  return coin < player.coinsSpent
                    ? "⚫️"
                    : coin < player.coins
                    ? "🟡"
                    : "⚪️";
                })}
                <br />
              </span>
            ))}
          </td>
        ))}
      </tr>
<tr key="playertime">
        <td colSpan={4}>
          <span style={{ float: "left" }}>
            {TIME_EMOJI} time spent: {player.timeSpent}
          </span>
          {isActive ? (
            <span
              style={{ cursor: "pointer", float: "right" }}
              onClick={discardSelection}
            >
              ❎ discard action
            </span>
          ) : (
            <span style={{ float: "right" }}>wait for your turn</span>
          )}
        </td>
      </tr>
      <tr key="actiontime">
        <td colSpan={4}>
          <span style={{ float: "left" }}>
            {TIME_EMOJI} current action:&nbsp;
            {actionTime(player.selection, resourcesFromMap(map, player.swarm))}
          </span>
        </td>
      </tr>
      <tr key="requiredresources">
        <td colSpan={4}>
          required resources:
          {ResourceList.map(
            (r) =>
              ` ${emojiFromResource(r)}: ${player.selection.cost.resources[r]}`
          )}
        </td>
      </tr>
      <tr key="providedresources">
        <td colSpan={4}>
          provided resources:
          {ResourceList.map(
            (r) =>
              ` ${emojiFromResource(r)}: ${
                resourcesFromMap(map, player.swarm)[r]
              }`
          )}
        </td>
      </tr>
    </tbody>
  </table>
);
