import type * as Swarms from "@hervan/swarms";
import { Fragment } from "react";
import { TIME_EMOJI, fontSize } from "../theme";
import { Token } from "./Token";

type MarketState = Swarms.Market.Market;

interface MarketProps {
  market: MarketState;
  discardToken: (...args: any[]) => void;
}

export const Market = ({ market, discardToken }: MarketProps) => (
  <table
    style={{
      fontSize: `${fontSize}vmin`,
    }}
  >
    <thead>
      <tr>
        <th key="market" colSpan={2}>
          market
        </th>
      </tr>
    </thead>
    <tbody>
      <tr key="stalls">
        <td key="stallcost" />
        <td
          key="stallhead"
          style={{
            verticalAlign: "top",
            lineHeight: "6vmin",
            textAlign: "right",
          }}
        >
          stalls
        </td>
        <td colSpan={5} key="tokens">
          {market.stalls.map((token, i) => (
            <Token key={i} token={token} />
          ))}
        </td>
      </tr>
      <tr key="discard" style={{ cursor: "pointer" }} onClick={discardToken}>
        <td
          key="discardcost"
          style={{
            verticalAlign: "top",
            lineHeight: "6vmin",
            textAlign: "right",
          }}
        >
          {TIME_EMOJI}
        </td>
        <td
          key="discardhead"
          style={{
            verticalAlign: "top",
            lineHeight: "6vmin",
            textAlign: "right",
          }}
        >
          discard
        </td>
        <td key="dtokens">
          {market.discard.map((token, i) => (
            <Fragment key={i}>
              <Token token={token} />
              {i % 10 === 9 ? <br /> : null}
            </Fragment>
          ))}
        </td>
      </tr>
      <tr key="bag">
        <td key="bagcost" />
        <td
          key="baghead"
          style={{
            verticalAlign: "top",
            lineHeight: "6vmin",
            textAlign: "right",
          }}
        >
          bag
        </td>
        <td key="btokens">
          {market.bag.map((token, i) => (
            <Fragment key={i}>
              <Token token={token} />
              {i % 10 === 9 ? <br /> : null}
            </Fragment>
          ))}
        </td>
      </tr>
    </tbody>
  </table>
);
