import { deepAssign, unique } from "@hervan/utils";
import { SwarmsState } from "index";
import {
  actionCost,
  blueprintMastery,
  isUniqueBlueprint,
  isValidSpaceSelection,
  upgradeGrid,
} from "..";
import { Action, ActionList } from "../../../Action";
import { Blueprint } from "../../../Blueprint";
import {
  Coordinates,
  TerrainMap,
  applyMapEffect,
  clusterCity,
  getAdjacentCoordinates,
  getAdjacentToSelection,
  sameCoordinates,
  subtractCoordinates,
  terrainCost,
} from "../../../Map";
import { getBoundingBox, terrainFromMap } from "../../../Map/Terrain";
import { addTokenToBag, discardMarketToken } from "../../../Market";
import { Player } from "../../../Player";

export type Slot<T extends Action> = {
  action: T;
  row: number;
  blueprint?: Blueprint;
  value: number;
};

type ActionRunner = (G: SwarmsState, id: number) => SwarmsState;

const selectSlot = (G: SwarmsState, id: number, row: number, action: Action) =>
  deepAssign(
    G,
    G.players[id].grid[row][action],
    "players",
    (p) => p.id === id,
    "selection",
    "slot"
  );

export const upgrade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const token = G.market.stalls[0];
  if (token === undefined) {
    return G;
  }
  const slot = currentPlayer.selection.slot;
  if (slot === undefined || slot.blueprint !== undefined) {
    return G;
  }
  if (
    !isValidSpaceSelection(currentPlayer.grid, slot.row, slot.action, token)
  ) {
    return G;
  }

  const grid = upgradeGrid(currentPlayer.grid, slot, token);
  const market = addTokenToBag(G.market, token);

  const upgradedGridGame = deepAssign(
    { ...G, market },
    grid,
    "players",
    (p) => p.id === id,
    "grid"
  );

  return selectSlot(upgradedGridGame, id, slot.row, slot.action);
};

const expand: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const location = currentPlayer.selection.location;
  const cost = terrainCost(location, G.map, actionCost(slot));
  const map = applyMapEffect(G.map, location, {
    state: "occupied",
    owner: id,
  });
  const coins =
    currentPlayer.coins +
    blueprintMastery(currentPlayer.grid, token, slot.value);
  const swarm = [...currentPlayer.swarm, ...location];
  const player = deepAssign(
    { ...currentPlayer, swarm, coins },
    cost,
    "selection",
    "cost",
    "resources"
  );
  const market = discardMarketToken(G.market);
  return deepAssign(
    { ...G, map, market },
    player,
    "players",
    (p) => p.id === id
  );
};

const build: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot?.blueprint === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const destination = currentPlayer.selection.location;
  const cost = actionCost(slot);
  const map = applyMapEffect(G.map, destination, {
    state: "built",
    blueprint: token,
  });
  const coins =
    currentPlayer.coins +
    blueprintMastery(currentPlayer.grid, token, slot.value);
  const city = clusterCity([...currentPlayer.city, destination]);
  const swarm = currentPlayer.swarm.filter(
    (c1) => !destination.some((c2: Coordinates) => sameCoordinates(c1, c2))
  );
  const player = deepAssign(
    { ...currentPlayer, city, swarm, coins },
    cost,
    "selection",
    "cost",
    "resources"
  );
  const market = discardMarketToken(G.market);
  return deepAssign(
    { ...G, map, market },
    player,
    "players",
    (p) => p.id === id
  );
};

const trade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const deliveryDistance = (
    location: Coordinates[],
    destination: Coordinates[]
  ) =>
    Math.max(
      ...Object.values(
        subtractCoordinates(
          getBoundingBox(location).topLeft,
          getBoundingBox(destination).topLeft
        )
      ).map(Math.abs)
    );
  const distance = deliveryDistance(
    currentPlayer.selection.location,
    currentPlayer.selection.destination
  );
  const targetOpponents = getAdjacentToSelection(
    currentPlayer.selection.destination
  )
    .map((c) => terrainFromMap(G.map, c))
    .map((t) => (t.state === "occupied" ? t.owner : id))
    .filter((playerId) => playerId !== id)
    .sort();
  const targetDeliveries = targetOpponents
    .filter(unique)
    .map((targetId) => G.players[targetId])
    .map((player) => {
      const tradeSlot = player.grid
        .map((row) => row[Action.Trade])
        .find(
          (action) =>
            action.blueprint === token ||
            (action.blueprint === undefined &&
              isUniqueBlueprint(player.grid, action.row, action.action, token))
        );
      if (tradeSlot === undefined) {
        return { ...player, tradeSlot, amount: 0 };
      }
      const demand =
        blueprintMastery(currentPlayer.grid, token, slot.value) +
        targetOpponents.filter((o) => o === player.id).length;
      const counterDemand =
        blueprintMastery(player.grid, token, tradeSlot.value) +
        player.city
          .map((b) => terrainFromMap(G.map, b[0]))
          .filter((t) => t.state === "built" && t.blueprint === token).length;
      const amount = demand - counterDemand;
      return {
        ...player,
        tradeSlot,
        amount,
      };
    })
    .filter(
      ({ tradeSlot, amount }) => tradeSlot !== undefined && amount > 0
    ) as (Player & {
    tradeSlot: Slot<Action>;
    amount: number;
  })[];
  const cost = terrainCost(
    currentPlayer.selection.destination,
    G.map,
    actionCost(slot, distance)
  );
  const map = applyMapEffect(G.map, currentPlayer.selection.destination, {
    state: "occupied",
    owner: id,
  });
  const amount = targetDeliveries.reduce((acc, p) => acc + p.amount, 0);
  const price = [...G.market.stalls, ...G.market.discard].filter(
    (t) => t === token
  ).length;
  const coins = price * amount + currentPlayer.coins;
  const swarm = [
    ...currentPlayer.swarm,
    ...currentPlayer.selection.destination,
  ];
  const delivery = currentPlayer.delivery + targetOpponents.length;
  const player = deepAssign(
    { ...currentPlayer, swarm, delivery, coins },
    cost,
    "selection",
    "cost",
    "resources"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const target = targetDeliveries.find((pd) => pd.id === p.id);
    if (target === undefined) {
      return p;
    }
    const grid = upgradeGrid(p.grid, target.tradeSlot, token);
    return {
      ...p,
      grid,
    };
  });
  const market = discardMarketToken(G.market);
  return { ...G, map, market, players };
};

export const scienceScore = (player: Player, map: TerrainMap) => [
  Math.trunc(
    player.swarm.filter((critter) =>
      getAdjacentCoordinates(critter)
        .map((adj) => terrainFromMap(map, adj))
        .some((adj) => adj.state === "occupied" && adj.owner !== player.id)
    ).length / 4
  ),
  Math.max(
    0,
    ...player.city.map((cluster) => Math.trunc(cluster.flat().length / 4))
  ),
  player.delivery,
  player.grid.filter((row) =>
    ActionList.every((a) => row[a].blueprint !== undefined)
  ).length +
    ActionList.filter((a) =>
      player.grid.every((row) => row[a].blueprint !== undefined)
    ).length,
];

const research: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return G;
  }
  const token = G.market.stalls[0];
  const cost = actionCost(slot);
  const coins =
    currentPlayer.coins +
    blueprintMastery(currentPlayer.grid, token, slot.value);
  const player = deepAssign(
    { ...currentPlayer, coins },
    cost,
    "selection",
    "cost",
    "resources"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const grid = upgradeGrid(p.grid, p.grid[slot.row][Action.Science], token);
    return { ...p, grid };
  });
  const market = discardMarketToken(G.market);
  return { ...G, market, players };
};

export const getActionRunner: (action: Action) => ActionRunner = (action) => {
  switch (action) {
    case Action.Swarm:
      return expand;
    case Action.City:
      return build;
    case Action.Trade:
      return trade;
    case Action.Science:
      return research;
  }
};
