import { Player as PlayerModule } from "@hervan/swarms";
import type { BoardProps } from "boardgame.io/react";
import { type SwarmsState } from "../Swarms";
import { Map } from "./Map";
import { Market } from "./Market";
import { Player } from "./Player";
import { TimeTrack } from "./TimeTrack";

const winner = PlayerModule.winner;

export interface GameProps extends BoardProps<SwarmsState> {}

export function Board(props: GameProps) {
  const displayPlayer = props.G.players[parseInt(props.playerID || "0")];

  return props.playerID ? (
    <>
      <Map
        map={props.G.map}
        selection={displayPlayer.selection}
        deselectLocation={props.moves.deselectLocation}
        deselectDestination={props.moves.deselectDestination}
        selectLocation={props.moves.selectLocation}
        selectDestination={props.moves.selectDestination}
      />
      <Market market={props.G.market} discardToken={props.moves.discardToken} />
      <TimeTrack players={props.G.players} />
      <Player
        map={props.G.map}
        selectSlot={props.moves.selectSlot}
        player={displayPlayer}
        displayResult={
          props.ctx.gameover && winner(props.G.players).id === displayPlayer.id
        }
        isActive={props.isActive}
        discardSelection={props.moves.discardSelection}
      />
    </>
  ) : (
    <>
      {props.G.players.map((player) => (
        <>
          <Player
            map={props.G.map}
            selectSlot={props.moves.selectSlot}
            player={player}
            displayResult={
              props.ctx.gameover && winner(props.G.players).id === player.id
            }
            isActive={props.isActive}
            discardSelection={props.moves.discardSelection}
          />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
        </>
      ))}
    </>
  );
}
