import type * as Swarms from "@hervan/swarms";
import { TIME_EMOJI } from "../theme";
import { Fragment } from "react";

interface PlayersProps {
  players: Swarms.Player.Player[];
}

export const TimeTrack = ({ players }: PlayersProps) => (
  <>
    {players.map(({ id, timeSpent }) => (
      <Fragment key={id}>
        Player {id}: {timeSpent}
        {TIME_EMOJI}
      </Fragment>
    ))}
  </>
);
