import * as Swarms from "@hervan/swarms";
import { deepAssign, numberlist } from "@hervan/utils";
import type { Game, Move } from "boardgame.io";
import { INVALID_MOVE } from "boardgame.io/core";

type Action = Swarms.Action.Action;
type Blueprint = Swarms.Blueprint.Blueprint;
type Coordinates = Swarms.Map.Coordinates;
type TerrainMap = Swarms.Map.TerrainMap;
type Market = Swarms.Market.Market;
type Player = Swarms.Player.Player;
type Track = Swarms.Track.Track;

const { Action, ActionList, actionTime, noSelection } = Swarms.Action;
const {
  getAllBlueprintCoordinatesWithTranslation,
  validateBuildBlueprint,
  validateManufactureBlueprint,
  validateBuildingBlueprint,
  validateDeliveryBlueprint,
  validateExpansionBlueprint,
} = Swarms.Blueprint;
const {
  GAME_SIZE,
  VECTOR_ORIENTATIONS,
  addCoordinates,
  applyMapEffect,
  containsCoord,
  getAdjacentToSelection,
  newMap,
  raiseMap,
  sameCoordinates,
  smoothMap,
  startCorners,
} = Swarms.Map;
const { addBags, payWithCoins } = Swarms.Map.Terrain.Resource;
const { discardMarketToken, newMarket, refillMarketTokens } = Swarms.Market;
const { newPlayer, nextPlayer, winner } = Swarms.Player;
const { actionCost, blueprintMastery, isValidSpaceSelection, upgradeGrid } =
  Swarms.Player.Grid;
const { getActionRunner, scienceScore, upgrade } = Swarms.Player.Grid.Slot;
const { moveOnTurnTrack } = Swarms.Track;
const { resourcesFromMap } = Swarms.Map.Terrain;

const GAME_LENGTH = 2;

export type SwarmsState = {
  map: TerrainMap;
  market: Market;
  players: Player[];
  turnTrack: Track;
  gameEndTriggered?: number;
};

const playerSetupSwarm: (
  G: SwarmsState,
  token: Blueprint,
  id: number
) => SwarmsState = (G, token, id) => {
  const currentPlayer = G.players[id];
  const grid = upgradeGrid(
    currentPlayer.grid,
    currentPlayer.grid[0][Action.Swarm],
    token
  );
  const move = getExpansionPattern(G.map, token, currentPlayer)[0];
  const player = { ...currentPlayer, grid, swarm: move };
  const map = applyMapEffect(G.map, move, { owner: id, state: "occupied" });
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const playerSetupState: (G: SwarmsState) => SwarmsState = (G) =>
  G.players.reduce(
    (acc, _, i) => playerSetupSwarm(acc, G.players[i].blueprint, i),
    G
  );

const triggerGameEnd = (G: SwarmsState, id: number) =>
  G.gameEndTriggered === undefined &&
  nextPlayer(G.players, G.turnTrack) !== id &&
  G.players[id].grid.filter(
    (row) => row[Action.Science].blueprint !== undefined
  ).length >= GAME_LENGTH;

export const passTime: (
  G: SwarmsState,
  id: number
) => SwarmsState | undefined = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer === undefined) {
    return G;
  }
  const actionCoins = payWithCoins(
    currentPlayer.selection.cost.resources,
    resourcesFromMap(G.map, currentPlayer.swarm)
  );
  const timeTaken = actionTime(
    currentPlayer.selection,
    resourcesFromMap(G.map, currentPlayer.swarm)
  );
  const timeSpent = currentPlayer.timeSpent + timeTaken;
  const turnTrack = moveOnTurnTrack(
    G.turnTrack,
    currentPlayer.timeSpent,
    timeSpent
  );
  const coinsSpent = currentPlayer.coinsSpent + actionCoins;
  if (coinsSpent > currentPlayer.coins) {
    return undefined;
  }

  const nextG = {
    ...G,
    players: G.players.map((p) =>
      p.id === currentPlayer.id ? { ...p, timeSpent, coinsSpent } : p
    ),
    turnTrack,
  };

  if (triggerGameEnd(nextG, currentPlayer.id)) {
    return { ...nextG, gameEndTriggered: timeSpent };
  }
  return nextG;
};

const discardToken: Move<SwarmsState> = ({ G, playerID, events }) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (G.market.stalls.length === 0) {
    return invalidMove();
  }
  const market = discardMarketToken(G.market);
  const time = currentPlayer.selection.cost.time + 1;
  const discardGame = passTime(
    deepAssign(
      { ...resetSelection(G, id), market },
      time,
      "players",
      (p) => p.id === id,
      "selection",
      "cost",
      "time"
    ),
    id
  );
  if (discardGame === undefined) return invalidMove();
  events.endTurn();
  return discardGame;
};

const deselectLocation: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.location.filter((c) => !sameCoordinates(c, coord)),
    "players",
    (p) => p.id === id,
    "selection",
    "location"
  );
};

const selectLocation: Move<SwarmsState> = (
  { G, playerID, events },
  coords: Coordinates[],
  action: Action
) => {
  const token = G.market.stalls[0];
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (containsCoord(currentPlayer.selection.location, coords[0])) {
    return deepAssign(
      G,
      currentPlayer.selection.location.filter(
        (c) => !sameCoordinates(c, coords[0])
      ),
      "players",
      (p) => p.id === id,
      "selection",
      "location"
    );
  }
  const location = [...currentPlayer.selection.location, ...coords];
  const selection = {
    ...currentPlayer.selection,
    location,
  };
  const validation =
    action === Action.Swarm
      ? validateExpansionBlueprint
      : action === Action.City
      ? validateBuildBlueprint
      : action === Action.Trade
      ? validateManufactureBlueprint
      : action === Action.Science
      ? validateBuildingBlueprint
      : () => false;
  const valid = validation(G.map, selection.location, token, currentPlayer);
  if (valid) {
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    if (location.length === 4 && action !== Action.Trade) {
      events.endTurn();
      return confirm(validG, id);
    }
    return validG;
  }
  return invalidMove();
};

const deselectDestination: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.destination.filter(
      (c) => !sameCoordinates(c, coord)
    ),
    "players",
    (p) => p.id === id,
    "selection",
    "destination"
  );
};

const selectDestination: Move<SwarmsState> = (
  { G, playerID, events },
  coords: Coordinates[]
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const destination = [...currentPlayer.selection.destination, ...coords];
  if (
    validateDeliveryBlueprint(
      G.map,
      G.market.stalls[0],
      currentPlayer.selection.location,
      destination,
      id
    )
  ) {
    const selection = {
      ...currentPlayer.selection,
      destination,
    };
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    if (destination.length === 4) {
      events.endTurn();
      return confirm(validG, id);
    }
    return validG;
  }
  return invalidMove();
};

const debugRaiseMap: Move<SwarmsState> = ({ G, random }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const raisedTerrains = raiseMap(originalTerrains, random.Number, 1);
  const improvedMap = raisedTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const debugSmoothMap: Move<SwarmsState> = ({ G }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const smoothTerrains = smoothMap(originalTerrains);
  const improvedMap = smoothTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const selectSlot: Move<SwarmsState> = (
  { G, playerID },
  row: number,
  action: Action
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const token = G.market.stalls[0];
  if (token === undefined) {
    return invalidMove();
  }
  const slot = currentPlayer.grid[row][action];
  if (isValidSpaceSelection(currentPlayer.grid, row, action, token)) {
    const cost = actionCost(slot);
    const selection = {
      ...currentPlayer.selection,
      slot,
      cost: {
        ...currentPlayer.selection.cost,
        resources: addBags(currentPlayer.selection.cost.resources, cost),
      },
    };
    return deepAssign(G, selection, "players", (p) => p.id === id, "selection");
  }
  return invalidMove();
};

const confirm: (
  G: SwarmsState,
  id: number
) => SwarmsState | "INVALID_MOVE" | undefined = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer.selection.slot === undefined) {
    return invalidMove();
  }
  const runner = getActionRunner(currentPlayer.selection.slot.action);
  const actionGame = passTime(runner(upgrade(G, id), id), id);
  if (actionGame === undefined) return invalidMove();
  return actionGame;
};

const resetSelection = (G: SwarmsState, id: number) =>
  deepAssign(G, noSelection, "players", (p) => p.id === id, "selection");

const discardSelection: Move<SwarmsState> = ({ G, playerID }) =>
  resetSelection(G, parseInt(playerID || "0"));

const distanceToCorner = (p: Player, c: Coordinates) => {
  const corner = startCorners()[p.id];
  return Math.abs(c.col - corner.col) + Math.abs(c.row - corner.row);
};

const sortDistanceToCorner = (p: Player, a: Coordinates[], b: Coordinates[]) =>
  Math.min(...a.map((aC) => distanceToCorner(p, aC))) -
  Math.min(...b.map((bC) => distanceToCorner(p, bC)));

const getExpansionPattern = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  getAdjacentToSelection(
    player.swarm.length > 0 ? player.swarm : [startCorners()[player.id]]
  )
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateExpansionBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, a, b))
    .slice(0, 1);

const getBuildPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.swarm
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateBuildBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, a, b))
    .slice(0, 1);

const getManufacturePatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.swarm
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateManufactureBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, b, a));

const getBuildingPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.city
    .flat()
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateBuildingBlueprint(map, bp, blueprint, player)
      )
    );

const getDeliveryDestination: (
  map: TerrainMap,
  location: Coordinates[],
  moveUnit: Coordinates,
  blueprint: Blueprint,
  id: number
) => Coordinates[] | undefined = (map, location, moveUnit, blueprint, id) =>
  numberlist(GAME_SIZE).reduce(
    (acc: { last: Coordinates[]; furthest: Coordinates[] | undefined }, _) => {
      const next = acc.last.map((c: Coordinates) =>
        addCoordinates(c, moveUnit)
      );
      return {
        last: next,
        furthest: validateDeliveryBlueprint(map, blueprint, location, next, id)
          ? next
          : acc.furthest,
      };
    },
    { last: location, furthest: undefined }
  ).furthest;

export const SwarmsGame: Game<SwarmsState> = {
  setup: ({ random, ctx }) => {
    const map: TerrainMap = newMap(random.Number);

    const playerOrder = numberlist(ctx.numPlayers);
    const players: Player[] = playerOrder.map((id) =>
      newPlayer(id, startCorners()[id])
    );
    const turnTrack: Track = [[...playerOrder]];
    const market = newMarket;
    const gameEndTriggered = undefined;

    const initialPlayerGame = {
      map,
      market,
      players,
      turnTrack,
      gameEndTriggered,
    };

    return playerSetupState(initialPlayerGame);
  },

  turn: {
    onBegin: ({ G, ctx, random }) => {
      const market =
        G.market.stalls.length === 0
          ? refillMarketTokens(G.market, random.Shuffle)
          : G.market;
      const id = parseInt(ctx.currentPlayer);
      return deepAssign(
        { ...G, market },
        G.players[id].selection,
        "players",
        (p) => p.id === id,
        "selection"
      );
    },
    order: {
      first: () => 0,
      next: ({ G }) => nextPlayer(G.players, G.turnTrack, G.gameEndTriggered),
    },
    onEnd: ({ G }) =>
      deepAssign(G, noSelection, "players", () => true, "selection"),
  },

  moves: {
    discardToken,
    selectSlot,
    deselectLocation,
    selectLocation,
    deselectDestination,
    selectDestination,
    discardSelection,
    debugRaiseMap,
    debugSmoothMap,
  },

  endIf: ({ G: { players, gameEndTriggered } }) => {
    if (gameEndTriggered !== undefined) {
      if (players.every((p) => p.timeSpent >= gameEndTriggered)) {
        return winner(players);
      }
    }
  },

  onEnd: ({ G }) => {
    const players = G.players.map((player) => {
      const coins = scienceScore(player, G.map).reduce((acc, score, i) => {
        const bpm = blueprintMastery(
          player.grid,
          player.grid[i][Action.Science].blueprint,
          player.grid[i][Action.Science].value
        );
        const res = score * bpm;
        return acc + res;
      }, player.coins);
      return { ...player, coins };
    });
    console.log({
      "player scores": players.map((p) => p.coins - p.coinsSpent),
      "time consumed": G.gameEndTriggered,
    });
    return { ...G, players };
  },

  ai: {
    enumerate: (G, _ctx, playerID) => {
      const id = parseInt(playerID || "0");
      const currentPlayer = G.players[id];
      if (G.market.stalls.length === 0) {
        return [{ move: "refillMarket" }];
      }
      const selection = currentPlayer.selection;
      const token = G.market.stalls[0];
      if (selection.slot === undefined) {
        const moves = currentPlayer.grid.flatMap((row, i) =>
          ActionList.filter(
            (action) =>
              isValidSpaceSelection(currentPlayer.grid, i, action, token) &&
              currentPlayer.coins - currentPlayer.coinsSpent >=
                payWithCoins(
                  actionCost(row[action]),
                  resourcesFromMap(G.map, currentPlayer.swarm)
                )
          ).map((action) => ({
            move: "selectSlot",
            args: [i, action],
          }))
        );
        if (moves.length === 0) {
          return [{ move: "discardToken" }];
        }
        return moves;
      }
      if (selection.location.length < 4) {
        switch (selection.slot.action) {
          case Action.Swarm: {
            const moves = getExpansionPattern(G.map, token, currentPlayer).map(
              (pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Swarm],
              })
            );
            if (moves.length === 0) {
              return [{ move: "discardToken" }];
            }
            return moves;
          }
          case Action.City: {
            const moves = getBuildPatterns(G.map, token, currentPlayer).map(
              (pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.City],
              })
            );
            if (moves.length === 0) {
              return [{ move: "discardToken" }];
            }
            return moves;
          }
          case Action.Trade: {
            const moves = getManufacturePatterns(G.map, token, currentPlayer)
              .filter(
                (pattern) =>
                  VECTOR_ORIENTATIONS.map((moveUnit) =>
                    getDeliveryDestination(G.map, pattern, moveUnit, token, id)
                  ).filter((pattern) => pattern !== undefined).length > 0
              )
              .map((pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Trade],
              }));
            if (moves.length === 0) {
              return [{ move: "discardToken" }];
            }
            return moves;
          }
          case Action.Science:
            const moves = getBuildingPatterns(G.map, token, currentPlayer).map(
              (pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Science],
              })
            );
            if (moves.length === 0) {
              return [{ move: "discardToken" }];
            }
            return moves;
        }
      }
      if (selection.destination.length < 4) {
        if (selection.slot.action === Action.Trade) {
          const moves = VECTOR_ORIENTATIONS.map((moveUnit) =>
            getDeliveryDestination(
              G.map,
              selection.location,
              moveUnit,
              token,
              id
            )
          )
            .filter((pattern) => pattern !== undefined)
            .map((pattern) => ({
              move: "selectDestination",
              args: [pattern],
            }));
          if (moves.length === 0) {
            return [{ move: "discardToken" }];
          }
          return moves;
        }
      }
      return [{ move: "discardToken" }];
    },
  },
};

const invalidMove = () => {
  const a = true || false;
  if (a) return INVALID_MOVE;
};
